import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProductProvider } from '../../providers/product/product'
import { Observable } from 'rxjs/Observable';
@IonicPage({
  segment: 'product/:id'
})
@Component({
  selector: 'page-product',
  templateUrl: 'product.html',
})
export class ProductPage {
  private product: Observable<any>;
  private tagId: string;
  constructor(public navCtrl: NavController, public navParams: NavParams, private productProvider: ProductProvider) {

  }

  ionViewDidLoad() {
    this.tagId = this.navParams.get('id');
    this.getProduct(this.tagId);
  }

  getProduct(id) {
    this.product = this.productProvider.getProductById(id);
  }
}



